<template>
    <div class="add-page">
		<div class="container">
			<p class="title">新增数据</p>
			<div class="partInput">
				<div class="item">
				    <span class="item-title">姓名</span>
				    <input type="text"
				           placeholder="请输入姓名" v-model="userInfo.name"></input>
				</div>
				<div class="item">
				    <span class="item-title">专业</span>
				    <input type="text"placeholder="请输入专业" v-model="userInfo.major"></input>
				</div>
				<div class="item">
				    <span class="item-title">性别</span>
				    <select v-model="userInfo.sex">
				        <option value="男">男</option>
				        <option value="女">女</option>
				    </select>
				</div>
				
				
			</div>
			<div class="add-btn">
			    <el-button size="small" type="primary" @click="add">添加</el-button>
			</div>
		</div>
        
    </div>
</template>

<script>
    import axios from 'axios'
    export default {
        name: "add",
        data(){
            return{
                userInfo:{
                    name:'',
                    sex:'男',
                    major:''
                }
            }
        },
        methods:{
            add(){
                axios.post(`http://127.0.0.1:8080/loginCheck/`).then(
                    response=>{
                        if(response.data.result=='true'){
							window.location.reload()
                        }
                        else{
                        	alert("添加失败")
						}
                    },
                    error=>{
                    	console.log(error.message)
					}
                )
            }

        }
    }
</script>

<style scoped>
.add-page{
	background-color: #ffffff;
	padding-top: 10px;
	box-shadow: 0 30px 30px #ccc;
}
p{
	color: #888888;
}
.partInput{

	text-align: left;
	width: 30%;
	margin: auto;
}
.item{
	margin-top: 10px;
}
.item input{
	margin-left: 15px;
	height: 20px;
	font-size: 15px;
	border-radius: 5px;
	border: 1px dotted #999999;

}
.item span{
	font-size: 15px;
	color: #888888;
}
.item select{
	margin-left: 15px;
	font-size: 15px;
	border-radius: 5px;
	border: 1px dotted #999999;
	color: #555555;


}
.add-btn{
	height: 50px;
	padding-top: 10px;
	margin-top: 5px;
	margin-left: 140px;
}

</style>