import vueRouter from 'vue-router'
import add from "@/components/add";
import selection from "@/components/selection";
import xiugai from '@/components/Eidit'
import list from '@/components/list'
export default new vueRouter({
    routes:[

		{
			name:'listtable',
			path:'/listtable',
			component: list,
			children:[
				{
					name:'Add',
					path:'/listtable/add',
					component:add
				},
				{
					name:'Selection',
					path:'/listtable/selection',
					component:selection
				},
				{
					name:'xiugai',
					path: '/listtable/xiugai',
					component: xiugai
				},
			]
		}
    ]
})