import Vue from 'vue'
import Vuex from 'vuex'
import axios from "axios";
Vue.use(Vuex)

const actions={
    changeListShow(miniStore,info){
        axios.post(`http://127.0.0.1:8080/loginCheck/?account=${info[0]}&password=${info[1]}`).then(
            response=>{
                console.log('获取到了值',response.data)
                if(response.data.result=='true'){
                    console.log('执行了if')
                    miniStore.commit('openListShow')

                }
                else {
                    alert('输入信息错误或用户不存在')
                }
            },
            error=>{
                console.log(error.message)
            }
        )
        // console.log(info[0],info[1])
        // console.log('in store actions')
    },
    registerListShow(miniStore,info){
        axios.post(`http://127.0.0.1:8080/registerCheck/?account=${info[0]}&password=${info[1]}`).then(
            response=>{
                if(response.data.result=='exit'){
                   alert("用户已存在")
                }
               else if (response.data.result=='true'){
                    miniStore.commit('openListShow')
                }
                else {
                    alert('注册失败')
                }
            },
            error=>{
                console.log(error.message)
            }
        )
        // console.log('in store actions')
    }
}
const mutations={
    changeIsShow(state){
        state.registerShow=!state.rigisterShow
        state.loginShow=!state.loginShow
        console.log(state.isShow,'have done')
    },
    changeTwoShow(state){
        state.loginShow=false
        state.registerShow=false
        console.log(state.isShow,'have done')
    },
    openListShow(miniState) {
        miniState.listShow=true
    },
    getId(miniState,value){
        miniState.id=value
    },
    changeIf(miniState){
        miniState.highIsShow=false
    }
}
const state={
    registerShow:false,
    listShow:false,
    id:'',
    loginShow:true
}
export default new Vuex.Store({
    actions,
    mutations,
    state
})