<template>
    <div class="list-page">
		<div class="container">
			<el-table
			        :data="tableData"
			        style="width: 100%"
			        :header-cell-style="headerRowStyle"
			        align="center"
			        :row-style="highLight"
			        max-height="500">
			    <el-table-column
			            label="学生信息管理">
			        <el-table-column
			                label="学生学号"
			                width="180"
			                prop="id">
			        </el-table-column>
			        <el-table-column
			                label="姓名"
			                width="180"
			                prop="name">
			        </el-table-column>
			        <el-table-column
			                label="性别"
			                width="180"
			                prop="sex">
			        </el-table-column>
			        <el-table-column
			                label="专业"
			                width="180"
			                prop="major">
			        </el-table-column>
			        <el-table-column >
			            <template slot-scope="scope">
			
			                <el-button
			                        size="mini"
			                        type="danger"
			                        @click="handleDelete(scope.$index, scope.row)">删除</el-button>
			            </template>
			        </el-table-column>
			    </el-table-column>
			</el-table>
			<div class="bottom-btn">
			    <el-button
			            size="mini"
						class="btn"
			            @click="handleAdd">添加</el-button>
			    <el-button
			            size="mini"
						class="btn"
			            @click="handleCha">查询</el-button>
			    <el-button
			            size="mini"
						class="btn"
			            @click="handleEdit">编辑</el-button>

				<router-view></router-view>
			</div>

		</div>
    </div>

</template>

<script>
    import axios from "axios";

    export default {
