                     name:'caknckke',
                     sex:'男',
                     major:'养猪'
                 }, ]
             }
         },
        methods: {
            handleEdit() {
                this.$router.push({
                    path:'/listtable/xiugai',
                    query:{
                        tableData:this.tableData
                    }
                })
            },
            handleDelete(index, row) {
                // console.log(index, row.id);

                axios.post(`http://127.0.0.1:8080/loginCheck/`).then(
                    response=>{
                        if(response.data.result=='true'){
							window.location.reload()
                        }
                    },
                    error=>{
                    	console.log(error.message)
					}
                )

            },
            handleAdd(){

                this.$router.push('/listtable/add')
            },
            handleCha(){
                this.$router.push('/listtable/selection')
            },
            headerRowStyle({rowIndex}){
              if(rowIndex==0)
                  return {
                      fontSize: '20px',
                      textAlign: 'center'
                  }
            },
            highLight({row}){
                const id=this.$store.state.id
                if(id && row.id==id){
                    return{
                        backgroundColor:'#faebd7'
                    }
                }

            }
        },
		mounted() {
        	axios.post(`http://127.0.0.1:8080/`).then(
        			response=>{
        				this.tableData=response.data
					},
					error=>{
        				console.log(error.message)
					}
			)
		}


	}
</script>

<style scoped>
.list-page{
	padding-top: 100px;
	padding-bottom:100px ;
	
}
.container{
	background-color: #ffffff;
	width: 50%;
	margin: auto;
	text-align: center;
	box-shadow: 0 0 30px #ccc;

}
.bottom-btn{
